@font-face {
  font-family: "robotoBold";
  src: url("../../fonts/Roboto-Regular.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}
.hero {
  max-width: 90rem;
  height: 100vh;
}
.hero__conteiner {
  display: grid;
  grid-template-columns: 1fr 1fr;
  height: 100%;
}
.hero__conteiner--left {
  position: relative;
  padding-top: 6rem;
}
.hero__conteiner--img {
  position: absolute;
  display: flex;
  right: 0%;
  width: 31.6rem;
  height: 20.6rem;
  background-image: url("../../images/sean-o-KMn4VEeEPR8-unsplash_1_s6zmfh_ar_4_3,c_fill,g_auto__c_scale,w_538.jpg");
}
.hero__conteiner--right {
  position: relative;
  left: 5.9rem;
}
@media (max-width: 1200px) {
  .hero__conteiner--right {
    left: 3rem;
  }
}
.hero__conteiner--title {
  position: absolute;
  height: 3.1rem;
  top: 8.5rem;
}
.hero__conteiner--title h1 {
  font-size: 2.6rem;
  line-height: 3.2rem;
  margin-bottom: 1.5rem;
}
.hero__conteiner--paragraph {
  position: absolute;
  top: 13.1rem;
  width: 23.8rem;
  height: 6rem;
}
.hero__conteiner--paragraph p {
  line-height: 1.5rem;
  margin-bottom: 2rem;
  font-size: 1rem;
}
.hero__conteiner--btn {
  position: absolute;
  cursor: pointer;
  padding: 1rem 2.5rem;
  border-radius: 32px;
  top: 21.1rem;
  background-color: #E4C1AE;
  border: none;
  color: #FFFFFF;
}

@font-face {
  font-family: "robotoBold";
  src: url("../../fonts/Roboto-Regular.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}
* {
  font-size: 16px;
  background-color: #E5E5E5;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "robotoBold", sans-serif;
  font-style: normal;
}